.projects__list{
	text-align: left;
	margin-top: 20px;
	position: relative;
	// z-index: -2;
}
.projects__list::after{
	position: absolute;
	content: "";
	width: 100%;
	height: 10px;
	background-color: #fefefe;
	bottom: 0;
	left: 0;
	z-index: -1;
}
.projects__item{
	padding-top: 20px;
	position: relative;
	overflow: hidden;
	height: auto;
	transition: all 0.5s ease;
}
// .projects__item::after{
// 	content: "";
// 	position: absolute;
// 	width: 100%;
// 	height: 1px;
// 	background-color: #191919;
// 	bottom: 0;
// 	left:0;
// 	z-index: -1;
// }
.projects__link{
	display: flex;
	align-items: flex-start;
	
}
.projects__smalltitle, .projects__count{
	font-family: "Roboto Condensed", sans-serif;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 21px;
	padding-bottom: 5px;
	display: inline-block;
}
.projects__smalltitle{
	display: flex;
	justify-content: space-between;
	align-items: baseline;
	margin-right: 4%;	
	// overflow: hidden;

}
.projects__wrap{
	display: flex;
	flex-direction: column;
	width: 95%;
	
	box-sizing: border-box;
}
.projects__count{
	display: block;
	margin-right: 3%;
	margin-left: 5px;
	// overflow: hidden;
}
.project__count-wrap{
	overflow: hidden;
}
.projects__smalltitle-wrap{
	overflow: hidden;
}
.projects__arrow{
	// float: right;
	width: 12px;
	height: 12px;
	// display: inline-block;
	// display: inline-block;
	background-image: url("../img/arrow.svg");
	background-size: 12px 12px;
	position: relative;
}

.arrow__hover-wrap{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	height: 0;
	transition: all 1s ease;
}
.arrow__hover{
	width: 12px;
	height: 12px;
	top: -1;
	display: block;
}

.projects__link:hover .arrow__hover-wrap{
	height: 100%;
}

.projects__smalltitle-wrap{
	width: 100%;
	transition: all 1s ease;
}
.projects__smalltitle-wrap .upper-animate{
	display: flex;
    justify-content: space-between;
    align-items: baseline;
}
.projects__link:hover .projects__smalltitle-wrap{
	color: #9E2426;
}
.projects__desc{
	display: inline-block;
	font-family: "Roboto Condensed", sans-serif;
	font-style: normal;
	font-weight: bold;
	font-size: 12px;
	line-height: 14px;
	padding-top: 5px;
	padding-bottom: 15px;
	margin-right: 20%;	
}
.projects__btn-wrap{
	text-align: center;
}
// .projects__more-btn{
// 	margin-top: 25px;
// 	margin-bottom: 80px;
// }
.projects__list{
	margin-bottom: 25px;
}
.projects{
	padding-bottom: 80px;
	
}


.projects__hidden{
	padding-top: 0;
	// display: none;
	height: 0;
	overflow: hidden;

}

.projects__smalltitle{
	position: relative;
}
.projects__smalltitle::after{
	content: "";
	position: absolute;
	width: 120%;
	height: 1px;
	background-color: #191919;
	left:-20%;
	bottom: 0;
	// bottom: 52%;
}
.projects__desc{
	overflow: hidden;
}
.projects__desc span{
	display: inline-block;
}
@media(min-width:767px){
	.projects__list{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.projects__item{
			width: 48%;
	}
	// .projects__hidden{
	// 	display: none;
	// }
}

@media(min-width:1170px){
	
