
.main__lang{
	margin-top: 6%;
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-size: 10px;
	line-height: 12px;
	text-transform: uppercase;
}
.hr{
	
	width: 98%;
	// height: 1px;
	margin-right: 2%;
	// background-color: #191919;

}

// .main__lang .hr .animate--line{
// 	transition-delay: 3000ms;
// }

.main__lang-item:last-child{
	padding-left: 10px;
	position: relative;
}
.main__lang-item:nth-child(2){
	padding-right: 10px;
}
.main__lang-item:last-child::before{
	content: "/";
	position: absolute;
	left: 0;
}
.main__lang-link{
	transition: all 0.2s ease;
}
.main__lang-link:hover,
.main__lang-link--current{
	color: #9E2426;
}

@media(min-width:1024px){
	// Языки
	.main__lang{
		// margin-top: 20px;
		margin-top: 3%;
		font-size: 18px;
		line-height: 21px;

	}
	.hr{
		width: 48%;
		
		margin-right: 3%;

	}

	.main__lang-item:last-child{
		padding-left: 18px;

	}
	.main__lang-item:nth-child(2){
		padding-right: 18px;
	}

}