.preloader{
	width: 100vw;
	height: 100vh;
	position: fixed;
	background-color: #181818;
	padding-left: 1%;
	padding-right: 1%;
	z-index: 5000;
	box-sizing: border-box;
	// transition: all 0.5s ease;
}
.preloader__bg{
	// content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	max-width: 100%;
	background-color: #FEFEFE;
	left:0;
	top: 0;
	width: 0;
	transition: width 0.35s ease-out;
	// z-index: 8000;
	transition-delay: 2s;
}

.main__logo--preloader{
	position: relative;
	display: inline-block;
	top: 50%;
	left: 48%;
	transform: translate(-48%, -50%);
	transition: all 0.5s ease-out;

	overflow: hidden;
	width: 18vh;
}




.preloader__wrap{
	position: absolute;
		left: 0;
		overflow: hidden;

		top: 0;
		width: 18vh;
		transition: all 4s ease-out;
		opacity: 0;

}
.preloader__wrap-forcustomer{
	opacity: 1;
}
.preloader__wrap-1{
	position: absolute;
	left: 0;
	overflow: hidden;
	// width: 18vh;
	top: 0;
	width: 18vh;


}
.main__logo-img--preloader--3{
	transform: translateX(101%);
	transition: all 0.2s ease-out;
	transition-delay: 2s;
}

.show__logo1{
 transform: translateX(0%);
}
