.portfolio__music-item{
	display: inline-block;
	margin-bottom: 40px;


}
.portfolio__music-wrap{
	display: inline-flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: flex-end;

	font-family: "Roboto Mono", monospace;

	// cursor: pointer;
}
.portfolio__music-title {
	display: flex;
	align-items: baseline;
	// width: 100%;
}
.portfolio__music-number{
	font-weight: normal;
	font-size: 36px;
	line-height: 102.19%;
	/* or 37px */
	font-family: "Roboto Mono", monospace;
	text-align: center;
	letter-spacing: 0.025em;
	text-transform: uppercase;
	background-color: #191919;
	padding: 14px 2px;
	padding-right: 30px;
	color: #FEFEFE;
	display: inline-block;
	margin-left: -1%;

}
.portfolio__music-text{
	font-family: "Roboto Mono", monospace;
	display: inline-block;
	font-style: normal;
	font-weight: 500;
	font-size: 24px;
	line-height: 28px;
	text-transform: uppercase;
	margin-left: -30px;
}
.portfolio__music{
	text-align: left;

}

.portfolio__music-title {
	position: relative;
}
.portfolio__music-desc{
	margin-top: 10px;
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 16px;
	padding-bottom: 10px;
	// border-bottom: 1px solid #191919;
	position: relative;
}
.portfolio__music-desc::after{
	content: "";
	position: absolute;
	// width: 100%;
	height: 1px;
	bottom:0;
	left: 0;
	background-color: #191919;
}
.portfolio__music-btn, .portfolio__music-btn--close{
	margin-top: 20px;
	margin-bottom: 20px;
	// margin-right: 45px;
}

.audio__wrap{
	position: relative;
	padding-bottom: 20px;
	overflow: hidden;
	transition: all 2s ease;
	// padding-right: 50px;
	
}
.audio__wrap::after{
	content: "";
	position: absolute;
	width: 108%;
	height: 1px;
	background-color: #191919;
	bottom: 0;
	left: 0;
}
// Аудио
.audio{
	// max-width: 100%;
	width: 90%;
	// box-sizing: border-box;
	position: relative;
	margin-bottom: 10px;
	vertical-align: middle;
	cursor: pointer;	
	transition: all 0.5s cubic-bezier(0.6, 0, 0.5, 1);
	
}
.audio:hover{
	padding-left: 13px;
}
.audio__row{
	// height: 20px;
	display: flex;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: center;
	font-family: "Roboto Condensed", sans-serif;
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	line-height: 16px;
	
}

// .elem-audio{
// 	display: none;
// }
.control-btn{
	width: 10%;
	height: 100%;
	background-image: url("../img/icon-play.svg");
	background-size: 8px 13px;
	background-position: left center;
	background-repeat: no-repeat;
}

.active .control-btn{
	background-image: url("../img/icon-pause.svg");
	background-size: 10px 10px;
}
.play.control-btn{
	background-image: url("../img/icon-play-white.svg");
	background-size: 8px 13px;
}
// .audio-icon{
// 	width: 20px;
// 	height: 10px;
// }
.counter{
	width: 22%;
}
.title{
	width: 60%;
}
.current{
	display: none;
}
.audio-progressbar{
	display: none;
}
.active{
	background-color: #191919;
}
.active .audio__row{ 
	height: 30px;
	// background-color: #191919;
	color: #FEFEFE;
	// padding-top: 5px;
	// padding-bottom: 5px;
	// padding-left: 13px;
}
.audio.active{
	padding-left: 13px;
	padding-right: 13px;
	background-color: #191919;
}
.active .total{
	display: none;
}

.active .current{
	display: block;
}
.active .audio-progressbar{
	display:block;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 3px;
}
.track{
	width: inherit;
	height: inherit;
	background: transparent;
}
.track-elem{
	width: 0;
	background-color: #9E2426;
	height: 100%;
	transition: all 0.5s ease;
}

.portfolio__music-desc--desktop{
	display: none;
}

// .audio__hidden{
// 	display: none;
// 	// overflow: hidden;
// 	// height: 0;
// 	// margin:0;
// }
.audio__row{
	height: 30px;
}



.portfolio__music-desc{
	overflow: hidden;
	// position: relative;

}
.upper-animate{
	// min-height: 16px;
	display: inline-block;
	position: relative;
	transform:translateY(120%);
	opacity: 0;
	transition: transform 2s cubic-bezier(.19,1,.22,1) 1s, opacity .5s linear 1s;
}
.upper-animate-show{
	opacity: 1;
	transform: translateY(0);
}

.audio{
	overflow: hidden;
}

.lower-animate{
	// display: inline-block;
	height: 0;
	position: relative;
	transform:translateY(-120%);
	opacity: 0;
	transition: transform 1s cubic-bezier(.19,1,.22,1) 1s, opacity .5s linear 1s;
}
.lower-animate-show{
	height: auto;
	opacity: 1;
	transform: translateY(0);
}
.audio__row{
	height: 30px;
	opacity: 1;
	transform: translateY(0);
	transition: transform 1s cubic-bezier(.19,1,.22,1), opacity .5s linear;
}

.audio__hidden{
	margin-bottom: 0px;
}
.audio__hidden .audio__row{
	// display: none;
	height: 0;
	position: relative;
	transform:translateY(-120%);
	opacity: 0;
}
.lower-animate{
	// display: inline-block;
	height: 0;
	position: relative;
	transform:translateY(-120%);
	opacity: 0;
	transition: transform 1s cubic-bezier(.19,1,.22,1) 1s, opacity .5s linear 1s;
}
.lower-animate-show{
	height: 30px;
	opacity: 1;
	transform: translateY(0);
}
@media(min-width:560px){
	.portfolio__music{
		margin-right: 10%;
	}
	.portfolio__music-item{
		display: block;
	}
	.portfolio__music-wrap{
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		font-family: "Roboto Mono", monospace;
		
	}
	.portfolio__music-desc{
		order: 3;
	}
	
	.portfolio__music-btn, .portfolio__music-btn--close{
		order: 2;
		// margin-left: 10%;
		// margin-top: 10px;
		margin-bottom: 0;
		margin-top:0;
	}
	.portfolio__music-desc--mobile{
		display: none;
	}
	.portfolio__music-desc--desktop{
		display: block;
	}

	.portfolio__music-desc--desktop{
		display: inline-block;
		margin-left: 15%;
		margin-bottom: 10px;
	}
	.audio__wrap{
		padding-left: 15%;
		padding-right: 19%;
		
	}

	.audio__wrap::after{
		content: "";
		position: absolute;
		width: 100%;
		height: 1px;
		background-color: #191919;
		bottom: 0;
		left: 0;
	}
	// 
	
}

