
.navigation{
	position: fixed;
	right: 0;
	bottom: 2%;
	z-index: 1000;
}
.navigation__list{
	font-style: normal;
	font-weight: normal;
	font-size: 2.2vh;
	line-height: 100%;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	text-align: center;
	color: #FEFEFE;
	vertical-align: middle;
}
.navigation__item {
	width: 18vh;
	
	background: #191919;
	margin-bottom: 20%;
	transform: translateX(16vh);
	cursor: pointer;
	transition: transform .5s;
	transition-timing-function: cubic-bezier(.7,0,.3,1);
}


.navigation__link{
	padding-top: 5px;
	padding-bottom: 5px;
	// display: block;
	display: inline-block;
}

