.navigation__item:hover{
	transform: translateX(0);
}
.navigation__item--contact{
	transform: translateX(0);
	background-color: #9E2426;
	// animation: nav-anim-contact 0.8s ease-out 5s, nav-anim-opacity 0.3s ease-out 5.1s forwards;


}





.navigation__item--hide{
	transform: translateX(16vh);
}
.navigation__item--show{
	transform: translateX(0);
	
}


@media(min-width:1024px){
	.navigation{
		position: fixed;
		right: 0;
		bottom: 5%;
	}
	.navigation__list{
		font-style: normal;
		font-weight: normal;
		font-size: 1.5vw;
		line-height: 100%;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		text-align: center;
		color: #FEFEFE;
		vertical-align: middle;
	}
	.navigation__item {
		width: 13vw;
		opacity: 0;
		
		background: #191919;
		margin-bottom: 25%;
		transform: translateX(11.5vw);
	}
	.navigation__link{
		padding-top: 10px;
		padding-bottom: 10px;
		display: block;
	}
	
	.navigation__item--contact{
		transform: translateX(0);
		background-color: #9E2426;
	
		
	}


	@keyframes nav-anim-opacity{
		100%{
			opacity: 1;
		}
	}
	@keyframes nav-anim{
		0%{
			transform: translateX(101%);
		}
		50%{
			transform: translateX(8.5vw);
		}
		100%{
			transform: translateX(11.5vw);
		}
	}
	@keyframes nav-anim-contact{
		0%{
			transform: translateX(101%);
		}
		50%{
			transform: translateX(0);
		}
		100%{
			transform: translateX(0);
		}
	}
	.navigation__item--hide{
		transform: translateX(11.5vw);
	}
	.navigation__item--show{
		transform: translateX(0);
		// transform: translateX(100%);
	}

	.navigation__item{
		animation: nav-anim 0.5s ease-out 5.8s, nav-anim-opacity 1s ease-out 5.8s forwards;
	
		// animation-name: nav-anim-opacity 0.5;
	}

	.navigation__item--contact{
		// transform: translateX(0);
		background-color: #9E2426;
		animation: nav-anim-contact 0.5s ease-out 5.8s, nav-anim-opacity 1s ease-out 5.5s forwards;
	
	
	}
	.navigation__item:nth-child(2){
		animation-delay: 6s;
	}
	.navigation__item:nth-child(3){
		animation-delay: 6.2s;
	}
	.navigation__item:nth-child(4){
		animation-delay: 6.4s;
	}
}