.contact{
	// background-color: #181818;
	padding-right: 20%;
	// color: #FEFEFE;
	padding-top: 25px;
	padding-bottom: 95px;
}

.contact__title{
	max-width: 400px;
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 28px;
	color: #FEFEFE;
	margin-bottom: 35px;
}
