.contact__phone{
	margin-bottom: 15px;
}
.contact__input{
	display: block;
	border:none;
	background: transparent;
	padding:0;
	margin:0;
	outline: none;
	box-sizing: border-box;
	// border-bottom: 1px solid #FEFEFE;
	resize:none;
	width: 100%;
	max-width: 320px;
	font-family: 'Roboto Condensed', sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 13px;
	line-height: 15px;
	padding-top: 10px;
	padding-bottom: 10px;
	margin-top: 20px;
	color: #FEFEFE;
	text-transform: uppercase;
	position: relative;
}
.input__wrap{
	// margin-top: 20px;
	position: relative;
}
.input__wrap::after{
	content: "";
	position: absolute;
	bottom:0;
	left: 0;
	width: 100%;
	height: 1px;
	background-color: #FEFEFE;
}
textarea{
	font-size: 13px;
	line-height: 15px;
}

.contact__input::placeholder{
	color: #FEFEFE;
}


.btn--white,
.contact__btn {
	box-sizing: border-box;
	text-align: center;
	
	outline: none;

}

.contact__btn {
	border: 0;
	font: inherit;
	color: #191919;
	background: 0 0;
	max-width: 320px;
	font-style: normal;
	font-weight: 400;
	font-size: 15.9101px;
	line-height: 19px;
	background: #fefefe;
	text-transform: uppercase;
	padding: 5px 0;
	margin: 30px 0 0;
	cursor: pointer;
	z-index: 100000;
	position: relative;
}



.btn--white {
	padding-left: 80px;
	padding-right: 80px;
	overflow: hidden
}

.btn--white span::before,
.btn--white::after,
.button::after {
	box-sizing: border-box;
	position: absolute;
	height: 100%;
	top: 0;
	transition-timing-function: cubic-bezier(.7, 0, .3, 1)
}

.btn--white span::before {
	content: attr(data-letters);
	color: #fefefe;
	font: inherit;
	left: 0;
	text-align: center;
	overflow: hidden;
	white-space: nowrap;
	z-index: 2;
	display: block;
	width: 0%;
	transition: width .5s
}

.btn--white:hover span::before,
.button:hover span::before,
img {
	width: 100%
}

.btn--white::after,
.button::after {
	content: "";
	width: 100%;
	right: 0;
	background: #191919;
	transition: transform .5s;
	transform: translate3d(101%, 0, 0)
}

.btn--white:hover::after,
.button:hover::after {
	transform: translateZ(0)
}

.btn--white,
.btn--white span {
	position: relative;
	display: block
}


.contact__link{
	position: relative;
	overflow: hidden;
	display: inline-block;

	transition: color .5s;
		// transform: translate3d(101%,0,0);
		transition-timing-function: cubic-bezier(.7,0,.3,1);
}
.contact__link {
	position: relative;
	display: inline-block;
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	line-height: 28px;
	display: block;
	color: #FEFEFE;
	font-family: "Roboto Mono",monospace;
	transition: color .5s;
	// transform: translate3d(101%,0,0);
	transition-timing-function: cubic-bezier(.7,0,.3,1);
}
.contact__link:hover{
	color: #9E2426;
}
.button-animate{
	position: relative;
}



@media(min-width:767px){
	.contact{
		padding-right: 5%;
	}
	.container--contact{
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}
	.contact__input{
		max-width: none;
	}
	.contact__column{
		width: 40%;
	}
	.input__wrap:first-child .contact__input{
		margin: 0;
		padding-top: 0;
	}
	.btn--white{
		padding-left: 80px;
		padding-right: 80px;
	}
}

@media(min-width:1170px){
	.container--contact{

		justify-content: space-between;

	}
	.contact{
		padding-right: 0%;
		padding-top: 60px;
		padding-bottom: 150px;
	}
	.contact__column:first-child{
		width: 40%;
	}
	.contact__title{
		// max-width: 590px;
		max-width: none;
	}
	.contact__column:last-child{
		width: 32%;
		padding-right: 12.5%;
	}
	.contact__title{
		font-size: 55px;
		line-height: 60px;
	}
	.contact__phone, .contact__email{
		font-size: 48px;
		line-height: 56px;

	}

	.contact__link span{
		position: relative;
		display: inline-block;
