.about__label{
	display: inline-block;
	// margin-left: 100%;
	font-family: "Roboto Mono", monospace;
	padding: 5px 35px;
	margin-left: -1%;
	margin-top: 45px;
  // background: #FEFEFE;
  position: relative;
  z-index: 10;
	color: #181818;
	text-align: left;
	font-style: normal;
	font-weight: 500;
	font-size: 30px;
	line-height: 29px;
	letter-spacing: 0.015em;
	margin-bottom: 15px;
}
.about__paragraph{
	font-size: 12px;
	line-height: 123.69%;
	color: #FEFEFE;
	font-weight: normal;
	padding-left: 35px;
	width: 71%;
	margin-bottom: 15px;
}
.about__wrapper{
	display: flex;
	flex-direction: column;
	
}
.about__img-wrap{
	text-align: right;
	
}
.about__img{
	width: 240px;
}

.glitch-img {
  display: inline-block;
  overflow: hidden;
  position: relative;
  // -webkit-filter: grayscale(1) contrast(130%);
  filter: grayscale(1) ;
}
.glitch-img > img:nth-child(2), 
.glitch-img > img:nth-child(3) {
  position: absolute;
  top: 0px;
  right: 0px;
}

.glitch-img > img:nth-child(2), .glitch-img > img:nth-child(3) {
  clip: rect(0, 0, 0, 0);
}

.glitch-img > img:nth-child(2) {
	right: 2px;

  
  animation: example-three-anim-1 5s infinite linear alternate-reverse;
}

.glitch-img > img:nth-child(3) {
	right: -2px;

  
  animation: example-three-anim-2 4s infinite linear alternate-reverse;
}
