.line {
	width: 100%;
	height: 1px;
	background-color: #191919;
}
.animate--line-about{
	background-color: #FEFEFE;
}
.animate--line{
	width: 0%;
	opacity: 0;
  
	transition: width 1.5s cubic-bezier(.19,1,.22,1),opacity .5s linear;
}
.animate-line-width{
	width: 100%;
	opacity: 1;
}

.animate--line-psevdo::after{
	width: 0;
	opacity: 0;
	transition-delay: 0.5s;
	transition: width 1.5s cubic-bezier(.19,1,.22,1),opacity .5s linear;
}

.animate--line-psevdo-width::after{
	width: 100%;
	opacity: 1;
	// transition: width 2s cubic-bezier(.19,1,.22,1),opacity .5s linear;
}

.projects__smalltitle.animate--line-psevdo-width::after{
	width: 120%;
	opacity: 1;
	// transition: width 2s cubic-bezier(.19,1,.22,1),opacity .5s linear;
}



