.has-animation {
	position: relative;
	display: inline-block;
}
.has-animation span  {
  opacity: 0;
}
.has-animation.animate-in span  {
  animation: textHidden 0.3s 0.5s forwards;
}
.has-animation.animate-in:before, .has-animation.animate-in:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  z-index: 10;
}
.has-animation.animate-in:before {
  background-color: #9E2426;
}
.has-animation.animate-in:after {
  background-color: #181818;
  animation-delay: .3s;
}



.has-animation.animation-ltr.animate-in:before {
  animation: revealLTR 1s ease;
}
.has-animation.animation-ltr.animate-in:after {
  animation: revealLTR 0.5s .3s ease;
}
.has-animation.animation-rtl.animate-in:before {
  animation: revealRTL 1s ease;
}
.has-animation.animation-rtl.animate-in:after {
  animation: revealRTL 0.5s .3s ease;
}
.has-animation.animation-rtl-backgound.animate-in:before{
	animation: revealRTL 1s ease;
}
.has-animation.animation-rtl-backgound.animate-in:after{
	animation: revealRTL-background 0.5s .3s ease forwards;
	z-index: -1;
}



.has-animation-bg-red.animate-in:before{
  background-color: #9E2426;
}
.has-animation-bg-red.animation-rtl-backgound.animate-in:after {
  background-color: #fefefe;
  animation-delay: .3s;
  // z-index: 2;
}

.has-animation-bg-black.animate-in:before{
  
  background-color: #181818;
  z-index: 5;
  
  // z-index: -1;
}
.has-animation-bg-black.animate-in:after {
  // background-color: #181818;
  background-color: #fefefe;
  animation-delay: .5s;
  z-index: 5;
  // z-index: -1;
}

@keyframes revealRTL {
  0% {
    width: 0;
    right: 0;
  }
  65% {
    width: 100%;
    right: 0;
  }
  100% {
    width: 0;
    right: 100%;
  }
}
@keyframes revealLTR {
  0% {
    width: 0;
    left: 0;
  }
  65% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes textHidden {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes revealRTL-background {
  0% {
    width: 0;
    right: 0;
  }
  65% {
    width: 100%;
    right: 0;
  }
  100% {
    width: 100%;
    right: 0%;
  }
}


