  .button-animate .button {
    transform: translateX(-101%);
    transition: all 0.5s ease; }
  .button-animate .btn--white {
    transform: translateX(-101%);
    transition: all 0.5s ease; }
  .button-show .button {
    transform: translateX(0%); }
  .button-show .btn--white {
    transform: translateX(0%); } }
