
.social{
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 10px;
	line-height: 12px;
	text-transform: uppercase;
}
.social--main{
	margin-bottom: 6%;
}
.hr--social{
	margin-left: 2%;
}



.social__item:first-child{
	padding-right: 10px;
	position: relative;
}
.social__item:nth-child(2){
	padding-left: 10px;
}
.social__item:first-child::after{
	content: "/";
	position: absolute;
	right: 0;
	top: 0;
}
.social__item{
	transition: all 0.2s ease;
}
.social__item:hover{
	color: #9E2426;
}



.social--about{
	color: #FEFEFE;
	padding-bottom: 20px;
}
.hr--social-about{
	// background-color: #FEFEFE;
	width: 100%;
	
}



@media(min-width:1024px){
	

	.social--main{
		margin-bottom: 3%;
		margin-left: 35%;
	}

	.social{
		
		font-size: 18px;
		line-height: 21px;

	}

	.hr--social{
		margin-left: 3%;
	}

	.social__item:first-child{
		padding-right: 18px;
		position: relative;
	}
	.social__item:nth-child(2){
		padding-left: 18px;
	}

	.social--about{
		color: #FEFEFE;
		margin-top: 80px;
	}
	.hr--social-about{
		width: 100%;
		// background-color: #FEFEFE;
	}

}