@import "preloader";
@import "main";
@import "lang";
@import "social";
@import "projects";
@import "portfolio";
@import "about";
@import "nav";
@import "contact";
@import "animate";

html{
  scroll-behavior: smooth;

	// perspective: 600px;
}
body{
	// perspective: 600px;
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Roboto Condensed', sans-serif;
	background-color: #FEFEFE;
	color: #191919;
	overflow-x: hidden;
  overflow: hidden;
}
.page-scroll{
  overflow-y: scroll;
}




ul{
	list-style: none;
	margin: 0;
	padding: 0;
}

h1,h2,h3,h4,h5, h6, p{
	padding: 0;
	margin:0;
}
img{
	width: 100%;
	height: auto;
}

a{
	color: inherit;
	text-decoration: none;
}

.bg{
	position: fixed;
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  width: 200%;
  height: 200vh;
  background: transparent url('../img/noisy-texture.png') repeat 0 0;
  background-repeat: repeat;
  animation: bg-animation .2s infinite;
  opacity: .9;
	visibility: visible;
	pointer-events: none;
	z-index: 8000;
}
.bg__black{
  background-color: #FEFEFE;
  transition: all 3s ease-out;
}

.bg__white{
  background-color: #181818;
}
@keyframes bg-animation {
	0% { transform: translate(0,0) }
	10% { transform: translate(-5%,-5%) }
	20% { transform: translate(-10%,5%) }
	30% { transform: translate(5%,-10%) }
	40% { transform: translate(-5%,15%) }
	50% { transform: translate(-10%,5%) }
	60% { transform: translate(15%,0) }
	70% { transform: translate(0,10%) }
	80% { transform: translate(-15%,0) }
	90% { transform: translate(10%,5%) }
	100% { transform: translate(5%,0) }
}

