.container{

	margin: 0 auto;
	min-width: 320px;
	max-width: 1023px;
	box-sizing: border-box;
	padding-left: 1%;
	padding-right: 1%;
}

.portfolio, .projects{
	margin-top: 80px;
	text-align: right;
}
.portfolio{
	margin-top: 60px;
}
.portfolio__title, .projects__title{
	display: inline-block;
	// margin-left: 100%;
	font-family: "Roboto Mono", monospace;
	padding: 12px;
	// background: #191919;
	color: #FEFEFE;

	font-style: normal;
	font-weight: 500;
	font-size: 30.4982px;
	line-height: 36px;
	text-align: center;
	margin-bottom: 40px;
}
.white{
	color: #FEFEFE;
}

.button {
	background: 0 0;
  border: 0;
  color: #fefefe;
	cursor: pointer;
	font-style: normal;
	font-weight: 400;
  padding: 5px 45px;
  padding-top: 5px;
  padding-bottom: 5px;
	text-align: center;
	letter-spacing: .115em;
	text-transform: uppercase;
	font-size: 13px;
	line-height: 128.19%;
	outline: 0;
	background-color: #9e2426;
	position: relative;
  overflow: hidden;
  transition: all 2.8s ease;
}

.button span,
.button span::before {
	display: block;
	text-align: center;
	position: relative
}

.button:hover {
	color: #dfdfdf
}

.button span::before {
	content: attr(data-letters);
	position: absolute;
	color: #dfdfdf;
	font: inherit;
	left: 0;
	width: 0%;
	top: 0;
	height: 100%;
	box-sizing: border-box;
	overflow: hidden;
	white-space: nowrap;
	transition: width .5s;
	transition-timing-function: cubic-bezier(.7, 0, .3, 1);
	z-index: 2
}



// .btn--white {
// 	padding-left: 80px;
// 	padding-right: 80px;
// 	overflow: hidden
// }

.btn--white span::before,
.btn--white::after,
.button::after {
	box-sizing: border-box;
	position: absolute;
	height: 100%;
	top: 0;
	transition-timing-function: cubic-bezier(.7, 0, .3, 1)
}

.btn--white span::before {
	content: attr(data-letters);
	color: #fefefe;
	font: inherit;
	left: 0;
	text-align: center;
	overflow: hidden;
	white-space: nowrap;
	z-index: 2;
	display: block;
	width: 0%;
	transition: width .5s
}

.btn--white:hover span::before,
.button:hover span::before,
img {
	width: 100%
}

.btn--white::after,
.button::after {
	content: "";
	width: 100%;
	right: 0;
	background: #191919;
	transition: transform .5s;
	transform: translate3d(-101%, 0, 0)
}

.btn--white:hover::after,
.button:hover::after {
	transform: translateZ(0)
}

.btn--white,
.btn--white span {
	position: relative;
	display: block
}



.button-animate{
  overflow: hidden;
}
.button-animate{
  display: inline-block;
  // width: 0;
  transition: all 2.8s ease;
}

.button-animate .button{
 transform: translateX(-101%);
 
  transition: all 0.5s ease;

}
.button-animate .btn--white{
  transform: translateX(-101%);
 
  transition: all 0.5s ease;
}
.button-show .button{
  transform: translateX(0%);
}
.button-show .btn--white{
  transform: translateX(0%);
}


