
.main__wrap{
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;

}

 
.main__logo-img{
	// width: 102px;
	// height: 225px;

	width: 18vh;
	// height: 10vw;
}
.main__logo{
	text-align: center;

}
.main__title{
	font-family: "Roboto Mono", monospace;
	font-style: normal;
	font-weight: bold;
	// font-size: 38px;
	font-size: 6vh;
	line-height: 97.19%;
	text-align: center;
	letter-spacing: 0.16em;
	text-transform: uppercase;

	margin-top: 8%;


}

.main__desc-list{

	margin-top: 8%;
	font-style: normal;
	font-weight: 500;
	font-size: 3.8vh;
	line-height: 100%;
	/* or 18px */
	letter-spacing: 0em;
	text-align: center;
	text-transform: uppercase;
	white-space:nowrap;


}

.main__desc-item:not(:last-child){
	margin-bottom: 5%;
}

.main__desc-item .has-animation span{
	padding: 5px;
}

.main__desc-item .has-animation {
	padding: 5px;
	margin-top: -5px;
	margin-bottom: -5px;
	margin-left: -5px;
}
.main__title-name-wrap .has-animation{
	padding: 6px;
	margin-top: -6px;
	margin-bottom: -6px;
	margin-left: -6px;
}

@media(min-width:1024px){

	// .main__title-name-wrap{
	// 	transform: translate3d(0,0,20px);
	// 	transform-style: preserve-3d;
	// }
	
	.main__wrap{
		height: 100vh;
		perspective: 1200px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
	
	}
	.main__title-wrap{
		display: flex;
		align-self: flex-start;
		width: 100%;
	}
	.main__title{
		width: 100%;
		display: flex;
		align-items: center;
	}
	
	
	.main__logo-img{
		width: 12.5vw;
		// height: 377px;
		// height: 100%;
		// width: 28.5vh;
	}
	
	.main__title{
		margin-left: 3%;
		font-size: 6.2vw;
		line-height: 100%;
		cursor: default;
		margin-top: 0%;
	}
	.main__title-name{
		padding: 6px;
	}
	.main__title-name-wrap{
		margin-top: -36px;
	}
	.has-animation:first-child .main__title-name{
		display: block;
		margin-bottom: 0%;
	
	}
	.main__desc-list{
		margin-left: 3%;
		margin-top: 0%;
		margin-top: -30px;
		font-style: normal;
		font-weight: 500;
		font-size: 2.5vw;
		line-height: 100%;
		/* or 18px */
		letter-spacing: 0em;
		text-align: left;
		

	}
	.main__desc-item:not(:last-child){
		margin-bottom: 7%;
	}

	.main__desc-item:nth-child(1)::before{
		left: -5px;
	}
	.main__desc-item:nth-child(3)::before{
		left: -5px;
	}
	.main__desc-item:nth-child(2)::before{
		left: 5px;
	}

	.main__desc-item .has-animation {
		padding: 3px;
	}
	
}